<template>
<div>
  <mu-select-field v-model="game1" :labelFocusClass="['label-foucs']" label="">
    <mu-menu-item v-for="text,index in list" :key="index" :value="index" :title="text" />
  </mu-select-field>
  <mu-raised-button label="" class="demo-raised-button" style="margin-left: 40px;" @click="selectProType(1)"/>
  <mu-raised-button label="" class="demo-raised-button" style="margin-left: 20px;" @click="selectProType(0)"/>
  <br/>
  <mu-text-field hintText="" v-model="title"/><br/>
  <div :style="showSelect">
    <mu-text-field hintText="A " v-model="anA"/><br/>
    <mu-text-field hintText="B " v-model="anB"/><br/>
    <mu-text-field hintText="C " v-model="anC"/><br/>
    <mu-text-field hintText="D " v-model="anD"/><br/>
    <mu-checkbox name="group" nativeValue="A" v-model="answer" label="A" class="demo-checkbox"/>
    <mu-checkbox name="group" nativeValue="B" v-model="answer" label="B" class="demo-checkbox"/>
    <mu-checkbox name="group" nativeValue="C" v-model="answer" label="C" class="demo-checkbox"/>
    <mu-checkbox name="group" nativeValue="D" v-model="answer" label="D" class="demo-checkbox"/>
    <div class="demo-checkbox">
      {{answer}}
    </div>
  </div>
  <div :style="showJudge">
    <mu-radio label="" name="group" nativeValue="" v-model="judgeVal" class="demo-radio"/> <br/>
    <mu-radio label="" name="group" nativeValue="" v-model="judgeVal"  class="demo-radio"/> <br/>
    <div class="demo-checkbox" style="margin-top: 30px;">
      {{judgeVal}}
    </div>
  </div>
  <mu-raised-button label="" class="demo-raised-button" style="margin-top: 20px;" primary/>
</div>
</template>
<script>
export default {
  data () {
    return {
      game1: 0,
      game2: ['1'],
      game3: '3',
      list: ['', '', '', '', '', ''],
      answer: [],
      title: '',
      anA: '',
      anB: '',
      anC: '',
      anD: '',
      showSelect: 'display:block;',
      showJudge: 'display:none;',
      judgeVal: ''
    }
  },
  methods: {
    selectProType(val){
      //
      if(val){
        this.showSelect = 'display:block;'
        this.showJudge = 'display:none;'
      }else{
        this.showJudge = 'display:block;'
        this.showSelect = 'display:none;'
      }
    }
  }
}
</script>


